<template>
    <div class="container">
        <div class="config" v-show="page === 1">
            <div class="title">配置七牛相关信息</div>
            <el-form ref="form" :rules="rules" :model="form" label-width="80px">
                <el-form-item label="存储空间(Bucket)" label-width='150px' prop="static">
                    <el-input v-model="form.static"></el-input>
                </el-form-item>
                <el-form-item label="AK(Access Key)" label-width='150px' prop="ak">
                    <el-input v-model="form.ak"></el-input>
                </el-form-item>
                <el-form-item label="SK(Secret Key)" label-width='150px' prop="sk">
                    <el-input v-model="form.sk"></el-input>
                </el-form-item>
                <el-form-item label="网址前缀" label-width='150px' prop="preSite">
                    <el-input v-model="form.preSite"></el-input>
                </el-form-item>
                <el-form-item label="参数" label-width='150px' prop="params">
                    <el-input v-model="form.params"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="saveFormToLocal">保存</el-button>
                    <div>此信息仅保存在本地</div>
                </el-form-item>
            </el-form>
        </div>
        <div class="upload" v-show="page === 2">
            <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item><a href="#config" @click="page = 1">配置</a></el-breadcrumb-item>
                <el-breadcrumb-item>上传</el-breadcrumb-item>
            </el-breadcrumb>
            <div class="title">上传图片</div>
            <el-upload
                    class="upload-demo"
                    ref="uploadNow"
                    drag
                    :action="actionPath"
                    :data="postData"
                    :on-success="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload"
                    multiple>
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
            <!--<a v-if="imageUrl" :href="imageUrlNew">-->
                <!--<img v-if="imageUrl" :src="imageUrl" class="avatar">-->
            <!--</a>-->
            <el-button id="copyBtn" ref="copyBtn" class="cpLink" type="primary" @click="doCopy">
                复制链接
            </el-button>
        </div>

    </div>
</template>

<script>
    import {Upload, Form, FormItem, Breadcrumb, BreadcrumbItem} from 'element-ui';
    import {genUpToken} from "../js/qiniuToken";

    export default {
        name: 'FishEditPic',
        components: {
            'el-upload': Upload,
            'el-form': Form,
            'el-form-item': FormItem,
            'el-breadcrumb': Breadcrumb,
            'el-breadcrumb-item': BreadcrumbItem
        },
        props: {
            msg: String
        },
        data() {
            return {
                actionPath:'https://upload.qiniup.com',
                postData: {},
                form: {
                    static: '',
                    ak: '',
                    sk: '',
                    preSite: '',
                    params: ''
                },
                imageUrl: '',
                imageUrlNew: '',
                message: '',
                rules: {
                    static: [
                        { required: true, message: '请输入存储空间', trigger: 'blur' },
                    ],
                    ak: [
                        { required: true, message: '请输入AK', trigger: 'blur' }
                    ],
                    sk: [
                        { required: true, message: '请输入SK', trigger: 'blur'}
                    ],
                    preSite: [
                        { required: true, message: '请输入前缀网址', trigger: 'blur'}
                    ]
                },
                page: 1
            }
        },
        created() {
            this.init()
            this._gToken()
        },
        methods: {
            _gToken() {
                var token;
                var policy = {};
                var bucketName = this.form.static;
                var AK = this.form.ak;
                var SK = this.form.sk;
                var deadline = Math.round(new Date().getTime() / 1000) + 3600;
                policy.scope = bucketName;
                policy.deadline = deadline;
                token= genUpToken(AK, SK, policy);
                this.postData.token=token;
            },
            handleAvatarSuccess(res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);
                this.imageUrlNew = `${this.form.preSite}/${res.key}${this.form.params}`
                this.doCopy()
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isPNG = file.type === 'image/png';
                const isGIF = file.type === 'image/gif';
                const isLt2M = file.size / 1024 / 1024 < 5;

                if (isJPG || isPNG || isGIF) {
                    if (!isLt2M) {
                        this.$message.error('上传头像图片大小不能超过 5MB!');
                        return false
                    }
                    return true;
                } else {
                    this.$message.error('上传头像图片只能是 JPG, PNG, GIF 格式!');
                    return false
                }

            },
            doCopy: function () {
                this.$copyText(this.imageUrlNew).then(() => {
                    this.$message({
                        message:'复制成功到剪切板！',
                        type:'success'
                    })
                }, () => {
                    this.$message({
                        message:'请点击下方的【复制链接】按钮！'
                    })
                })
            },
            saveFormToLocal() {
                if (this.form.static && this.form.ak && this.form.sk) {
                    window.localStorage.setItem('config', JSON.stringify(this.form))
                    this.page = 2
                }
            },
            init() {
                const config = window.localStorage.getItem('config')
                if (config) {
                    this.form = JSON.parse(config)
                    this.page = 2
                }
            }
        }

    }
</script>

